  cursor: default;
  background: #c9ecfc;
  border: 2px solid #c9ecfc;
  border-radius: 5px;
  width: 100%;
  height: 8px;
  transition: all .2s;
  box-shadow: 1px 1px 1px #0003, 0 0 1px #0d0d0d33;
}

[type="range"]::-webkit-slider-thumb {
  box-sizing: border-box;
  cursor: pointer;
  -webkit-appearance: none;
  background: #71a5be;
  border: 2px solid #3a667a;
  border-radius: 10px;
  width: 20px;
  height: 20px;
