  border-radius: 5px;
  width: 100%;
  height: 4px;
  transition: all .2s;
  box-shadow: 1px 1px 1px #0003, 0 0 1px #0d0d0d33;
}

[type="range"]::-moz-range-thumb {
  box-sizing: border-box;
  cursor: pointer;
  background: #71a5be;
  border: 2px solid #3a667a;
  border-radius: 10px;
  width: 20px;
  height: 20px;
  box-shadow: 4px 4px 4px #0003, 0 0 4px #0d0d0d33;
}

[type="range"]::-ms-track {
  cursor: default;
  color: #0000;
  background: none;
  border-width: 10px 0;
  border-color: #0000;
  width: 100%;
  height: 8px;
  transition: all .2s;
}

[type="range"]::-ms-fill-lower {
  background: #b4e4fb;
  border: 2px solid #c9ecfc;
  border-radius: 10px;
  box-shadow: 1px 1px 1px #0003, 0 0 1px #0d0d0d33;
}

[type="range"]::-ms-fill-upper {
  background: #c9ecfc;
  border: 2px solid #c9ecfc;
  border-radius: 10px;
