}

[type="range"]::-ms-thumb {
  box-sizing: border-box;
  cursor: pointer;
  background: #71a5be;
  border: 2px solid #3a667a;
  border-radius: 10px;
  width: 20px;
  height: 20px;
  margin-top: 2px;
  box-shadow: 4px 4px 4px #0003, 0 0 4px #0d0d0d33;
}

[type="range"]:disabled::-webkit-slider-thumb {
  cursor: not-allowed;
}

[type="range"]:disabled::-moz-range-thumb {
  cursor: not-allowed;
}

[type="range"]:disabled::-ms-thumb {
